<template>
<div class="nav">
    <ul class="nav">
        <router-link to="/about" tag="li" activeclass="active" exact><a>About</a></router-link>
        <router-link to="/bookmarker" tag="li" activeclass="active"><a>Bookmarker</a></router-link>
        <router-link to="/hello" tag="li" activeclass="active"><a>Profile</a></router-link>  
        <li role="presentation"><a @click="logout">Sign out</a></li>      
        <!-- <router-link to="/user/10" tag="li" activeclass="active"><a>User10</a></router-link>
        <router-link to="/user/20" tag="li" activeclass="active"><a>User20</a></router-link> -->
    </ul>
</div>
</template>

<script>
import firebase from 'firebase';
export default {
    name: 'Header',
    methods: {
        logout() {
            firebase.auth().signOut().then( () => {
                this.$router.push( { name: 'Login'});
            })
        }
    },
}
</script>

<style scoped>
.nav ul {
  list-style: none;
  background-color: #444;
  text-align: center;
  padding: 0;
  margin: 0;
}
.nav li {
  font-family: 'Oswald', sans-serif;
  font-size: 1.2em;
  line-height: 40px;
  height: 40px;
  border-bottom: 1px solid #888;
}
 
.nav a {
  text-decoration: none;
  color: #fff;
  display: block;
  transition: .3s background-color;
}
 
.nav a:hover {
  background-color: #005f5f;
}
 
.nav a.active {
  background-color: #fff;
  color: #444;
  cursor: default;
}
 
@media screen and (min-width: 600px) {
  .nav li {
    width: 120px;
    border-bottom: none;
    height: 50px;
    line-height: 50px;
    font-size: 1.4em;
  }
 
  /* Option 1 - Display Inline */
  .nav li {
    display: inline-block;
    margin-right: -4px;
  }
}
 
  /* Options 2 - Float
  .nav li {
    float: left;
  }
  .nav ul {
    overflow: auto;
    width: 600px;
    margin: 0 auto;
  }
  .nav {
    background-color: #444;
  }*/
</style>


